@charset "utf-8";

hr {
	margin-top: 40px;
	margin-bottom: 40px;
}

img {
	width: 100%;
	padding-bottom: 40px;
}

.video figure {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	margin: 0 0 20px;
}

.video iframe, .video object, .video embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.jumbotron {
	padding-bottom:20px;
}

#footer {
	min-height: 0;
}

